import "./header.scss";
import { Button } from "react-bootstrap";
import Image from "react-bootstrap/Image";
import Logo from '../../resource/logo.jpg';
import { NavLink } from "react-router-dom";
import { useState } from "react";

let active = {
  color: "rgb(81 200 245)",
};
let disactive = {
  color: "black"
};


const Header = (props) => {
  const [showStateBurger, setshowStateBurger] = useState(true)

  const showBurger = () => {
    let menuBurger = document.querySelector('.burger');

    console.log(menuBurger);
    if (showStateBurger) {
      menuBurger.classList.add("hide");
      menuBurger.classList.add("burger-anime");
      menuBurger.classList.remove("burger-close");
      setshowStateBurger(showStateBurger=>false)
    } else {
      menuBurger.classList.remove("hide");
      menuBurger.classList.remove("burger-anime");
       menuBurger.classList.add("burger-close");
       setshowStateBurger(showStateBurger => true);
    }

  }

  return (
    <div className="container">
      <header className="App-header">
        <NavLink to="/">
          <Image src={Logo} className="logo rounded"></Image>
        </NavLink>

        <div className="nav">
          <NavLink
            to="/development"
            style={({ isActive }) => (isActive ? active : disactive)}
            onClick={props.scrollUp}
          >
            Розробка
          </NavLink>

          <NavLink
            to="/support"
            style={({ isActive }) => (isActive ? active : disactive)}
          >
            Підтримка
          </NavLink>

          <NavLink
            to="/portfolio"
            style={({ isActive }) => (isActive ? active : disactive)}
            onClick={props.scrollUp}
          >
            Портфоліо
          </NavLink>

          <NavLink
            to="/contact"
            style={({ isActive }) => (isActive ? active : disactive)}
            onClick={props.scrollUp}
          >
            Контакти
          </NavLink>
        </div>

        <Button className="burger-menu" onClick={showBurger}>
          |||
        </Button>
      </header>
    </div>
  );
}

export default Header;
